<template>
	<view class="container">
		<watermark></watermark>
		<view class="tabs_box">
			<view class="title">新增客户</view>
			<barDropDown :chartData="chartData" :unitText="'人次'" :timeUnit="form.timeUnit" :timeSelect="timeSelect"
			 @prevTap="_prevTap" @nextTap="_nextTap" @unitTap="_unitTap"></barDropDown>
		</view>
		<view class="express_box" v-for="(item,index) in pieData" :key="index">
			<view class="title">{{item.name}}</view>
			<pieEcharts :Column="item" :canvasId="'pie' + index"></pieEcharts>
		</view>
		<tabBar></tabBar>
	</view>
</template>

<script>
	import tabBar from '@/components/tabBar2.vue';
	import pieEcharts from '@/components/echartsComponent/pieEcharts.vue';
	import barDropDown from '@/components/barDropDown.vue';
	import watermark from '@/components/watermark.vue';
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	import { bus } from '@/utils/bus.js';
	export default {
		data() {
			return {
				isshow: false,
				form: {
					timeUnit: '2', // 时间统计 0年 1季 2月 3周
					time: '', // 当前统计格式 prev/next值
				},
				timeSelect: { // 柱状图选择
					prev: '',
					next: '',
				},
				// 柱状图
				chartData: {
					categories: [],
					series: [{
						data: []
					}],
				},
				pieData: [], // 饼图数据
				// pieChart: [], // 饼图结构
				// pieChart:{ 
				// 	series:[]
				// },
			}
		},
		onLoad(){
			this.getList()
		},
		methods:{
			getList() {
				Tools.showLoading('加载中')
				let that = this
				httpUtils.sendHttpRequest('4004', Object.assign({}, that.form), function(res) {
					Tools.hideLoading()
					if (res.data.code == '0') {
						that.isshow = true
						that.timeSelect.prev = res.data.data.histogramData.pre
						that.timeSelect.next = res.data.data.histogramData.next
						that.chartData.series[0].data = res.data.data.histogramData.yData
						that.$set(that.chartData, 'series', that.chartData.series)
						that.$set(that.chartData, 'categories', res.data.data.histogramData.xData)
						bus.$emit('changeEcharts',that.chartData)
						// 饼图赋值
						that.pieData = res.data.data.pieData
						// that.pieData.forEach((item,index)=>{
						//   that.$nextTick(() => {
						// 		// that.$refs[`pieChart${index}`][0].showColumn(item)
						// 		that.$refs.pieChart[index].showColumn(index, item)
						// 		console.log(that.$refs.pieChart[index].showColumn(index,item))
						// 	})
						// })
					}
				})
			},
			// 柱状图左翻
			_prevTap() {
				this.form.time = this.timeSelect.prev
				this.getList()
			},
			// 柱状图右翻
			_nextTap() {
				this.form.time = this.timeSelect.next
				this.getList()
			},
			// 柱状图单位选择
			_unitTap(index) {
				this.form.timeUnit = index
				this.form.time = ''
				this.getList()
			},
		},
		components: {
			tabBar, barDropDown, pieEcharts, watermark
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f6f6f6;
	}
	.tabs_box{
		background-color: #fff;
		.title{
			font-size: 32rpx;
			margin-bottom: 20rpx;
			font-weight: bold;
			color: #333;
			padding: 20rpx 30rpx 0;
		}
	}
</style>
